@use 'base/variables' as v;
@use 'base/mixins' as m;

.bundles {
    &__grid {
        @include m.container;
        @include m.grid(1, 3rem);

        @include m.tablet {
            @include m.grid(3, 3rem);
            align-items: flex-start
        }
    }

    &__submit {
        margin-top: 3rem;
        border-radius: 0.5rem;
        @include m.button(v.$blue, v.$white);
    }
}

.bundle {
    @include m.card;

    &:nth-child(2) {
        order: -1;

        @include m.tablet {
            order: 0;
        }
    }

    &:nth-child(1) {
        order: 1;

        @include m.tablet {
            order: -1;
        }
    }

    &__list {
        @include m.reset-list;
        line-height: 2;
    }

    &__price {
        font-size: 6rem;
        margin: 3rem 0;
        color: v.$blue;
        font-weight: v.$heavy;
        text-align: center;
    }
}