@use 'base/variables' as v;

.actions {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;

    &--center {
        justify-content: center;
    }

    &__link {
        color: v.$black;
        font-size: 1.4rem;
        font-weight: v.$light;

        &:hover {
            color: v.$blue;
        }
    }
}