@use 'base/variables' as v;
@use 'base/mixins' as m;

.blocks {
    @include m.container;

    @include m.tablet {
        margin-top: 10rem;
    }

    &__grid {
        @include m.grid(1, 3rem);

        @include m.desktop {
            @include m.grid(2, 3rem);
        }
    }
}

.block {
    padding: 3rem;
    color: v.$white;
    border-radius: 1rem;

    &:nth-child(1) {
        @include m.bg-gradient(#F97216);
    }

    &:nth-child(2) {
        @include m.bg-gradient(#A855F7);
    }

    &:nth-child(3) {
        @include m.bg-gradient(#508408);
    }

    &:nth-child(4) {
        @include m.bg-gradient(#17769E);
    }

    &__heading {
        margin: 1rem 0 2rem 0;
        font-size: 3.6rem;
    }

    &__text {
        margin: 1rem 0;

        &--amount {
            margin-top: 5rem;
            font-weight: v.$heavy;
            font-size: 6rem;
            text-align: center;
        }
    }
}