@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$light-gray;

    &__header {
        background-color: v.$dark-gray;
        padding: 2rem;
    }

    &__header-grid {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__logo {
        @include m.logo;
        @include m.fluid-text(2rem, 3rem);
    }

    &__navigation {
        
    }

    &__form {
        display: flex;
        justify-content: center;
        flex-direction: column;
        @extend .dashboard__container;
        @include m.container-md;
    }
    
    &__grid {
        display: flex;
        flex: 1;
        overflow-y: auto;
    }
    
    &__sidebar {
        background-color: lighten(v.$dark-gray, 10%);
        width: 12rem;
    }
    
    &__menu {
        padding: 1rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        
        &-text {
            font-size: 1.1rem;
            text-transform: uppercase;
            font-weight: v.$bold;
            margin-top: 0.5rem;
        }
    }
    
    &__link {
        color: lighten(v.$dark-gray, 40%);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;
        transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;
        
        &:hover,
        &--active {
            background-color: lighten(v.$dark-gray, 20%);
            color: v.$white;
            border-radius: 1rem;
        }
    }
    
    &__icon {
        font-size: 2rem;
    }
    
    &__content {
        padding: 1rem 1rem 3rem 1rem;
        width: 100%;
        overflow-y: scroll;
        
        @include m.tablet {
            padding: 1rem 5rem 3rem 5rem;
        }
    }
    
    &__button-container {
        @include m.tablet {
            display: flex;
            justify-content: flex-end;
            margin-bottom: 5rem;
        }
    }

    &__button {
        @include m.button(v.$blue, v.$white);
        text-transform: uppercase;
        font-size: 1.4rem;
        border-radius: 0.5rem;
        display: flex;
        gap: 1rem;
        
        @include m.tablet {
            width: auto;
        }
    }
    
    &__container {
        @include m.shadow;
        background-color: v.$white;
        padding: 2rem;
        border-radius: 1rem;
    }
    
    &__submit--logout {
        @include m.link;
    }

    &__graph {
        @include m.container-sm;
    }
}