@use 'base/variables' as v;
@use 'base/mixins' as m;

.summary {
    background-image: linear-gradient(to right, rgba(v.$blue, 0.7), rgba(v.$blue, 0.9)), url(../img/bg_numbers.jpg);
    background-image: linear-gradient(to right, rgba(v.$blue, 0.7), rgba(v.$blue, 0.9)), image-set(
        url(../img/bg_numbers.avif) 1x, 
        url(../img/bg_numbers.webp) 1x, 
        url(../img/bg_numbers.jpg) 1x
    );
    padding: 12rem 0;
    background-size: cover;
    background-position: center;

    &__grid {
        @include m.container;
        @include m.grid(1, 5rem);

        @include m.tablet {
            @include m.grid(2, 5rem);
        }
    }

    &__block {
        text-align: center;
        color: v.$white;
    }

    &__text {
        font-weight: v.$heavy;
        line-height: 1;
        margin: 0;
        font-size: 2.4rem;
        text-transform: uppercase;
        
        &--number {
            @extend .summary__text;
            font-size: 8rem;
        }
    }
}